    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.navbar {
    background-color: #000000;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    margin: 0px;
}

.menu-bars {
    margin-left:  2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu{
    background-color: #000;
    weight: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position:fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left:0;
    transition:350ms;

}

.nav-text {
    display: flex;
    justify-content: start;
    align-items:center;
    padding: 0px 0px 0px 0px;
    list-style: none;
    height: 45px;
}

.nav-text a {
    text-decoration: none;
    color:#f5f5f5;
    font-size: 18px;
    width: 95%;
    height:100%;
    display:flex;
    align-items:center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a.hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #000000;
    width:100%;
    height:80px;
    display:flex;
    justify-content: start;
    align-items:center;
}

span{
    margin-left: 16px;
}
